import { Suspense } from "react";

import { getMealWithSimilar } from "@/app/actions/meals";

import MealIngredients from "@/components/MealIngredients";
import CategoryMeals from "@/components/CategoryMeals";
import MealDetails from "@/components/MealDetails";

export default function MealPage({ params }: { params: { id: string } }) {
  const mealData = getMealWithSimilar(params.id);

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex flex-col lg:flex-row gap-8">
        <div className="lg:w-2/3">
          <Suspense
            fallback={<div className="text-center">Loading meal recipe...</div>}
          >
            <MealDetails mealPromise={mealData.then(({ meal }) => meal)} />
            <MealIngredients mealPromise={mealData.then(({ meal }) => meal)} />
          </Suspense>
        </div>

        <Suspense
          fallback={<div className="text-center">Loading similar meals...</div>}
        >
          <CategoryMeals
            categoryPromise={mealData.then(({ meal }) => meal.strCategory)}
            mealsPromise={mealData.then(({ similarMeals }) => similarMeals)}
          />
        </Suspense>
      </div>
    </div>
  );
}
